@import url('https://fonts.googleapis.com/css2?family=Sarabun:wght@300&display=swap');
.body{
    overflow:hidden;
    max-width: 100vh;
}
.landing-page {
    min-height: 100vh;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
}
.Particles{

}
.landing-page > .rowIcons{
    text-align: center;
    align-content: center;
}
.landing-page > .info{
    text-align: center;
    font-size: 20pt;
    font-family: 'Sarabun', sans-serif;
    padding-bottom: 20px;
}
.landing-page > .card-title{
    text-align:center;
    font-size: 20pt;
    font-family: 'Sarabun', sans-serif;
}
.landing-page > .confirm_selection {
    font-family: 'Sarabun', sans-serif;
    font-size: 60pt;
    font-weight: bold;
}
.landing-page > .confirm_selection {
    animation: glowMain 1.5s infinite alternate;
}

@keyframes glowMain {
    to {
        text-shadow: 0 0 10px rgb(14, 250, 191);
    }
}


.Java, .faJs, .faCss3, .HTML{
    color: #434261;
    margin-left: 30px    ;

}
.React{
    color: #434261;
}

.about-me-page{
    min-height: 80vh;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
}
.rutgers{
    animation: glowRed 1.5s infinite alternate;

}
@keyframes glowRed {
    to {
        text-shadow: 0 0 10px rgb(241, 32, 95);
    }
}

.description{
    align-content: center;
}

.cardSliderWrapper{
    position:'absolute';
    overflow-x:'hidden';
}
.cardHolder{
    transition: transform 700ms cubic-bezier(0.455, 0.03, 0.515, 0.955); 
}
.card{
    height:"400px";
    width:"45vh";
    box-shadow:"10px 5px 10px rgba(136, 140, 125, 0.93)";
    
    
}
.cardLeftButton:hover, .cardRightButton:hover{
    cursor: grab;
    filter:drop-shadow(5px 1px 3px rgba(39, 39, 38, 0.93))

}
.cardLeftButton, .cardRightButton{
    filter:drop-shadow(5px 1px 3px rgba(136, 140, 125, 0.93))
}
.card-header{
    font-family: 'Sarabun', sans-serif;
    font-weight:bold;
    
}
.card-body{
    font-family: 'Sarabun', sans-serif;
    width:"45vh";
    text-align: center;
    background-image: linear-gradient(to right, rgba(90, 200, 204, 0.7), rgba(90, 200, 204,.6), rgba(90, 200, 204,.7));
    color:#434261;
    
}
.link{
    font-family: 'Sarabun', sans-serif;
    text-decoration: none;
    color: #434261;
}
.link:hover{
    text-decoration: none;
    color:#22204b;
}
.contactmeStart{
    text-align: center;
    font-family: 'Sarabun', sans-serif;
    font-size: 20pt;
}
.col{
    align-items: center;
}



    